import React from 'react';
import { Link } from 'react-router-dom';
import { Avatar, Divider, Tag, Typography } from 'antd';
import { EnvironmentOutlined } from '@ant-design/icons';
import { Listing as ListingData } from '../../../../lib/graphql/queries/Listing/__generated__/Listing';
import { iconColor } from '../../../../lib/utils';

interface Props {
	listing: ListingData['listing'];
}

const { Paragraph, Title, Text } = Typography;

export const ListingDetails = ({ listing }: Props) => {
	const {
		title,
		description,
		guidelines,
		image,
		type,
		energy,
		address,
		city,
		host,
		numOfSeats,
		numOfDoors,
	} = listing;

	return (
		<div className="listing-details">
			<div
				style={{ backgroundImage: `url(${image})` }}
				className="listing-details__image"
			/>
			<div className="listing-details__information">
				<Paragraph
					type="secondary"
					ellipsis
					className="listing-details__city-address"
				>
					<Link to={`/listings/${city}`}>
						<EnvironmentOutlined style={{ color: iconColor }} /> {city}
					</Link>

					<Divider type="vertical" />
					{address}
				</Paragraph>
				<Title level={3} className="listing-details__title">
					{title}
				</Title>
			</div>
			<Divider />
			<div className="listing-details__section">
				<Link to={`/user/${host.id}`}>
					<Avatar src={host.avatar} size={64} />
					<Title level={2} className="listing-details__host-name">
						{host.name}
					</Title>
				</Link>
			</div>

			<Divider />

			<div className="listing-details__section">
				<Title level={4}>About this car</Title>
				<div className="listing-details__about-items">
					<Tag color="volcano">{type}</Tag>
					<Tag color="volcano">{energy}</Tag>
					<Tag color="volcano">{numOfSeats} seats</Tag>
					{numOfDoors && <Tag color="volcano">{numOfDoors} doors</Tag>}
				</div>

				<div>
					<Text strong>Description</Text>
					<Paragraph ellipsis={{ rows: 3, expandable: true }}>
						{description}
					</Paragraph>
				</div>

				{guidelines && (
					<div>
						<Text strong>guidelines</Text>
						<Paragraph ellipsis={{ rows: 3, expandable: true }}>
							{guidelines}
						</Paragraph>
					</div>
				)}
			</div>
		</div>
	);
};
