import React, { useState } from 'react';
import { Link, Redirect } from 'react-router-dom';
import { useMutation } from '@apollo/react-hooks';
import {
	Form,
	Input,
	InputNumber,
	Layout,
	Typography,
	Radio,
	Upload,
	Button,
} from 'antd';
import { LoadingOutlined, PlusOutlined } from '@ant-design/icons';
import { UploadChangeParam } from 'antd/lib/upload';
import { Viewer } from '../../lib/types';
import { HOST_LISTING } from '../../lib/graphql/mutations';
import {
	HostListing as HostListingData,
	HostListingVariables,
} from '../../lib/graphql/mutations/HostListing/__generated__/HostListing';
import { ListingEnergy, ListingType } from '../../lib/graphql/globalTypes';
import {
	displayErrorMessage,
	displaySuccessNotification,
} from '../../lib/utils';

interface Props {
	viewer: Viewer;
}

const { Content } = Layout;
const { Text, Title } = Typography;
const { Item } = Form;

const getBase64Value = (
	img: File | Blob,
	callback: (imageBase64Value: string) => void
) => {
	const reader = new FileReader();
	reader.onload = () => {
		callback(reader.result as string);
	};
	reader.readAsDataURL(img);
};

const beforeImageUpload = (file: File) => {
	const fileIsValidImage =
		file.type === 'image/jpeg' || file.type === 'image/png';

	const fileIsValidSize = file.size / 1024 / 1024 < 1;

	if (!fileIsValidSize) {
		displayErrorMessage(
			"You're only able to upload valid image files of under 1MB in size!"
		);
		return false;
	}

	if (!fileIsValidImage) {
		displayErrorMessage("You're only able to upload valid JPG or PNG files!");
		return false;
	}

	return fileIsValidImage && fileIsValidSize;
};

export const Host = ({ viewer }: Props) => {
	const [form] = Form.useForm();

	const [imageLoading, setImageLoading] = useState(false);
	const [imageBase64Value, setImageBase64Value] = useState<string | null>(null);

	const [hostListing, { data, loading }] = useMutation<
		HostListingData,
		HostListingVariables
	>(HOST_LISTING, {
		onCompleted: () => {
			displaySuccessNotification(
				"Congratulations! You've successfully created your listing!"
			);
		},
		onError: () => {
			displayErrorMessage(
				"Sorry! We weren't able to create your listing. Please try again! Make sure you enter valid address, city and country."
			);
		},
	});

	const handleImageUpload = (info: UploadChangeParam) => {
		const { file } = info;

		if (file.status === 'uploading') {
			setImageLoading(true);
			return;
		}

		if (file.status === 'done' && file.originFileObj) {
			getBase64Value(file.originFileObj, (imageBase64Value) => {
				setImageBase64Value(imageBase64Value);
				setImageLoading(false);
			});
		}
	};

	if (!viewer.id || !viewer.hasWallet) {
		return (
			<Content className="host-content">
				<div className="host__form-header">
					<Title level={3} className="host__form-title">
						You'll have to be signed in and connected with Stripe to host a
						listing!
					</Title>

					<Text type="secondary">
						We only allow users who've signed in to our application and have
						connected with Stripe to host new listings. You can sign in at the{' '}
						<Link to="/login">Login</Link> page and connect with Stripe shortly
						after.
					</Text>
				</div>
			</Content>
		);
	}

	if (loading) {
		return (
			<Content className="host-content">
				<div className="host__form-header">
					<Title level={3} className="host__form-title">
						Please wait a sec!
					</Title>
					<Text>We are creating your listing now!</Text>
				</div>
			</Content>
		);
	}

	if (data && data.hostListing) {
		return <Redirect to={`/listing/${data.hostListing.id}`} />;
	}

	const handleHostListing = async () => {
		try {
			const values = await form.validateFields();

			const {
				title,
				description,
				guidelines,
				type,
				energy,
				address,
				numOfSeats,
				numOfDoors,
				city,
				state,
				country,
				postalCode,
				price,
			} = values;

			const fullAddress = `${address}, ${city}, ${state} ${postalCode}, ${country}`;

			const input = {
				title,
				description,
				guidelines,
				type,
				energy,
				numOfSeats,
				numOfDoors,
				address: fullAddress,
				country,
				image: imageBase64Value as string,
				price: price * 100,
			};

			hostListing({
				variables: {
					input,
				},
			});
		} catch (err) {
			displayErrorMessage('Please complete all required form fields!');
		}
	};

	return (
		<Content className="host-content">
			<Form form={form} layout="vertical" onFinish={handleHostListing}>
				<div className="host__form-header">
					<Title level={3} className="host__form-title">
						Let your car work while you finally take that vacay!
					</Title>
					<Text type="secondary">
						In this form, we'll collect some basic and additional information
						about your car.
					</Text>
				</div>

				<Item
					name="type"
					label="Vehicle Type"
					rules={[{ required: true, message: 'Please select vehicle type!' }]}
				>
					<Radio.Group name="type">
						<Radio.Button value={ListingType.CAR}>
							<span>Car</span>
						</Radio.Button>
						<Radio.Button value={ListingType.SUV}>
							<span>SUV</span>
						</Radio.Button>
						<Radio.Button value={ListingType.VAN}>
							<span>Van</span>
						</Radio.Button>
						<Radio.Button value={ListingType.MINIVAN}>
							<span>Minivan</span>
						</Radio.Button>
						<Radio.Button value={ListingType.TRUCK}>
							<span>Truck</span>
						</Radio.Button>
					</Radio.Group>
				</Item>

				<Item
					name="energy"
					rules={[{ required: true, message: 'Please select vehicle energy!' }]}
					label="Energy Type"
				>
					<Radio.Group>
						<Radio.Button value={ListingEnergy.GAS}>
							<span>Gas</span>
						</Radio.Button>
						<Radio.Button value={ListingEnergy.DIESEL}>
							<span>Diesel</span>
						</Radio.Button>
						<Radio.Button value={ListingEnergy.ELECTRIC}>
							<span>Electric</span>
						</Radio.Button>
						<Radio.Button value={ListingEnergy.HYBRID}>
							<span>Hybrid</span>
						</Radio.Button>
					</Radio.Group>
				</Item>

				<Item
					name="title"
					label="Title"
					extra="Max character count of 45"
					rules={[
						{
							required: true,
							message: 'Please enter a title for your listing!',
						},
					]}
				>
					<Input maxLength={45} placeholder="Tesla Model X 2020" />
				</Item>

				<Item
					label="Description"
					extra="Max character count of 400"
					name="description"
					rules={[
						{
							required: true,
							message: 'Please enter description for your listing!',
						},
					]}
				>
					<Input.TextArea
						rows={3}
						maxLength={400}
						placeholder="Midnight Silver Metallic Exterior with Black Interior. Fully loaded with premium package and bio defense air filter! Perfect for traveling anywhere!"
					/>
				</Item>

				<Item
					label="Guidelines"
					extra="Max character count of 400"
					name="guidelines"
				>
					<Input.TextArea
						rows={3}
						maxLength={400}
						placeholder="Smoking/Vaping/Food/Drinks and pets are NOT allowed inside the car."
					/>
				</Item>

				<Item
					name="numOfSeats"
					rules={[
						{ required: true, message: 'Please enter a number of seats!' },
					]}
					label="Number of Seats"
				>
					<InputNumber min={1} placeholder="4" />
				</Item>

				<Item name="numOfDoors" label="Number of Doors">
					<InputNumber min={1} placeholder="4" />
				</Item>

				<Item
					name="address"
					rules={[
						{
							required: true,
							message: 'Please enter address for your listing!',
						},
					]}
					label="Address"
				>
					<Input placeholder="565 5th Ave" />
				</Item>
				<Item
					name="city"
					rules={[
						{ required: true, message: 'Please enter city for your listing!' },
					]}
					label="City/Town"
				>
					<Input placeholder="New York" />
				</Item>
				<Item
					name="state"
					rules={[
						{
							required: true,
							message: 'Please enter state or province for your listing!',
						},
					]}
					label="State/Province"
				>
					<Input placeholder="New York" />
				</Item>

				<Item
					name="country"
					rules={[
						{
							required: true,
							message: 'Please enter country for your listing!',
						},
					]}
					label="Country"
				>
					<Input placeholder="United States" />
				</Item>

				<Item
					name="postalCode"
					rules={[
						{
							required: true,
							message: 'Please enter ZIP or postal code for your listing!',
						},
					]}
					label="Zip/Postal Code"
				>
					<Input placeholder="Please enter a zip code for your listing!" />
				</Item>

				<Item
					name="image"
					rules={[
						{ required: true, message: 'Please upload an image of your car!' },
					]}
					label="Image"
					extra="Images have to be under 1MB in size and of type JPG or PNG"
				>
					<div className="host__form-image-upload">
						<Upload
							// name="image"
							listType="picture-card"
							showUploadList={false}
							action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
							beforeUpload={beforeImageUpload}
							onChange={handleImageUpload}
						>
							{imageBase64Value ? (
								<img src={imageBase64Value} alt="Listing" />
							) : (
								<div>
									{imageLoading ? <LoadingOutlined /> : <PlusOutlined />}
									<div className="ant-upload-text">Upload</div>
								</div>
							)}
						</Upload>
					</div>
				</Item>

				<Item
					name="price"
					rules={[
						{ required: true, message: 'Please enter price for your listing!' },
					]}
					label="Price"
					extra="All prices in $USD/day"
				>
					<InputNumber min={0} placeholder="120" />
				</Item>

				<Item>
					<Button type="primary" htmlType="submit">
						Submit
					</Button>
				</Item>
			</Form>
		</Content>
	);
};
