import React from 'react';
import { Link, RouteComponentProps } from 'react-router-dom';
import { useQuery } from '@apollo/react-hooks';
import { Col, Row, Layout, Typography } from 'antd';
import { LISTINGS } from '../../lib/graphql/queries/Listings';
import { ListingsFilter } from '../../lib/graphql/globalTypes';
import {
	Listings as ListingsData,
	ListingsVariables,
} from '../../lib/graphql/queries/Listings/__generated__/Listings';
import { HomeHero, HomeListings, HomeListingsSkeleton } from './components';
import { displayErrorMessage } from '../../lib/utils';

import mapBg from './assets/map-background.jpg';
import sanFranciscoImage from './assets/san-fransisco.jpg';
import cancunImage from './assets/cancun.jpg';

const { Content } = Layout;
const { Paragraph, Title } = Typography;

const PAGE_LIMIT = 4;
const PAGE_NUMBER = 1;

export const Home = ({ history }: RouteComponentProps) => {
	const { data, loading, error } = useQuery<ListingsData, ListingsVariables>(
		LISTINGS,
		{
			variables: {
				filter: ListingsFilter.PRICE_HIGH_TO_LOW,
				limit: PAGE_LIMIT,
				page: PAGE_NUMBER,
			},
		}
	);

	const onSearch = (value: string) => {
		const trimmedValue = value.trim();

		if (trimmedValue) {
			history.push(`/listings/${trimmedValue}`);
		} else {
			displayErrorMessage('Please enter a valid search term');
		}
	};

	const renderListingsSection = () => {
		if (loading) {
			return <HomeListingsSkeleton />;
		}
		if (data) {
			return (
				<HomeListings title="Premium Cars" listings={data.listings.result} />
			);
		}
		return null;
	};

	return (
		<Content className="home" style={{ backgroundImage: `url(${mapBg})` }}>
			<HomeHero onSearch={onSearch} />

			<div className="home__cta-section">
				<Title level={2} className="home__cta-section-title">
					Way better than a rental car
				</Title>
				<Paragraph>
					Book unforgettable cars from trusted hosts around the world
				</Paragraph>
				<Link
					to="/listings/united%20states"
					className="ant-btn ant-btn-primary ant-btn-lg home__cta-section-button"
				>
					Popular listings in the US
				</Link>
			</div>

			{renderListingsSection()}

			<div className="home-listings">
				<Title level={4} className="home__listings-title">
					Our Suggestions
				</Title>
				<Row gutter={12}>
					<Col xs={24} sm={12}>
						<Link to="/listings/san%20francisco">
							<div className="home__listings-img-cover">
								<img
									src={sanFranciscoImage}
									alt="San Francisco"
									className="home__listings-img"
								/>
							</div>
						</Link>
					</Col>
					<Col xs={24} sm={12}>
						<Link to="/listings/cancn">
							<div className="home__listings-img-cover">
								<img
									src={cancunImage}
									alt="CanCn"
									className="home__listings-img"
								/>
							</div>
						</Link>
					</Col>
				</Row>
			</div>
		</Content>
	);
};
