import React from 'react';
import { useMutation } from '@apollo/react-hooks';
import moment, { Moment } from 'moment';
import { CardElement, useStripe, useElements } from '@stripe/react-stripe-js';
import { Modal, Button, Divider, Typography } from 'antd';
import { CarOutlined } from '@ant-design/icons';
import {
	displayErrorMessage,
	displaySuccessNotification,
	formatListingPrice,
} from '../../../../lib/utils';
import { StripeCardElement } from '@stripe/stripe-js';
import { CREATE_BOOKING } from '../../../../lib/graphql/mutations';
import {
	CreateBooking as CreateBookingData,
	CreateBookingVariables,
} from '../../../../lib/graphql/mutations/CreateBooking/__generated__/CreateBooking';

interface Props {
	id: string;
	price: number;
	checkInDate: Moment;
	checkOutDate: Moment;
	modalVisible: boolean;
	setModalVisible: (modalVisible: boolean) => void;
	clearBookingData: () => void;
	handleListingRefetch: () => Promise<void>;
}

const { Paragraph, Text, Title } = Typography;

export const ListingCreateBookingModal = ({
	id,
	price,
	checkInDate,
	checkOutDate,
	modalVisible,
	setModalVisible,
	clearBookingData,
	handleListingRefetch,
}: Props) => {
	const stripe = useStripe();
	const elements = useElements();

	const [createBooking, { loading }] = useMutation<
		CreateBookingData,
		CreateBookingVariables
	>(CREATE_BOOKING, {
		onCompleted: () => {
			clearBookingData();
			displaySuccessNotification(
				"Your've successfully booked the car! Enjoy your trip!",
				'Booking history can always be found in your User page.'
			);
			handleListingRefetch();
		},
		onError: () => {
			displayErrorMessage(
				"Sorry! We weren't able to book the car. Please try again later!"
			);
		},
	});

	const daysBooked = checkOutDate.diff(checkInDate, 'days') + 1;
	const listingPrice = price * daysBooked;
	const companyFee = 0.05 * listingPrice;
	const totalPrice = listingPrice + companyFee;

	const handleCreateBooking = async () => {
		if (!stripe || !elements) {
			return displayErrorMessage(
				"Sorry! We weren't able to connect with Stripe!"
			);
		}
		const cardElement = elements.getElement(CardElement);

		let { token: stripeToken, error } = await stripe.createToken(
			cardElement as StripeCardElement
		);

		if (stripeToken) {
			createBooking({
				variables: {
					input: {
						id,
						source: stripeToken.id,
						checkIn: moment(checkInDate).format('YYYY-MM-DD'),
						checkOut: moment(checkOutDate).format('YYYY-MM-DD'),
					},
				},
			});
		} else {
			displayErrorMessage(
				error && error.message
					? error.message
					: "Sorry! We weren't able to book the car. Please try again later!"
			);
		}
	};

	return (
		<Modal
			visible={modalVisible}
			centered
			footer={null}
			onCancel={() => setModalVisible(false)}
		>
			<div className="listing-booking-modal">
				<div className="listing-booking-modal__title">
					<Title className="listing-booking-modal__intro-title">
						{/* <CheckOutlined /> */}
						<CarOutlined />
					</Title>
					<Title className="listing-booking-modal__intro-title" level={3}>
						Book your car
					</Title>
					<Paragraph>
						Enter your payment information to book the listing from the dates
						between{' '}
						<Text mark strong>
							{moment(checkInDate).format('MMMM Do YYYY')}
						</Text>{' '}
						and{' '}
						<Text mark strong>
							{moment(checkOutDate).format('MMMM Do YYYY')}
						</Text>
						, inclusive.
					</Paragraph>
				</div>

				<Divider />

				<div className="listing-booking-modal__charge-summary">
					<Paragraph className="listing-booking-modal__charge-summary-total">
						{formatListingPrice(price, false)} * {daysBooked} days ={' '}
						<Text strong>{formatListingPrice(listingPrice, false)}</Text>
					</Paragraph>

					{/* <Paragraph>
						Flaircnc Fee <sub> ~5%</sub> ={' '}
						<Text strong>{formatListingPrice(companyFee)}</Text>
					</Paragraph> */}
					<Paragraph>
						Total = <Text mark>{formatListingPrice(listingPrice, false)}</Text>
					</Paragraph>
				</div>

				<Divider />

				<div className="listing-booking-modal__stripe-card-section">
					<CardElement
						options={{ hidePostalCode: true }}
						className="listing-booking-modal__stripe-card"
					/>
					<Button
						size="large"
						type="primary"
						className="listing-booking-modal__cta"
						loading={loading}
						onClick={handleCreateBooking}
					>
						Book
					</Button>
				</div>
			</div>
		</Modal>
	);
};
