import { gql } from 'apollo-boost';

export const LISTING = gql`
	query Listing($id: ID!, $bookingsPage: Int!, $limit: Int!) {
		listing(id: $id) {
			id
			title
			description
			guidelines
			image
			host {
				id
				name
				avatar
				hasWallet
			}
			type
			energy
			country
			city
			address
			price
			milesPerGallon
			numOfSeats
			numOfDoors
			bookings(limit: $limit, page: $bookingsPage) {
				total
				result {
					id
					renter {
						id
						name
						avatar
					}
					checkIn
					checkOut
				}
			}
			bookingsIndex
		}
	}
`;
